// step three, choice 5 of the flow https://miro.com/app/board/uXjVO_x__QE=/

import {
    Form,
    FormGroup, HelperText, HelperTextItem, TextInput, Title
} from '@patternfly/react-core';
import React, {ReactElement, useEffect, useState} from 'react';
import PropTypes from 'prop-types';
import {UserChoices} from "../CommonInterfaces/user-choices";
import {SamlMetadataFile} from "../../../Components/FileUpload/SamlMetadataFile/saml-metadata-file";
import {SERVICE_PROVIDER_ENTITY_ID} from "../CommonInterfaces/idp-request-types";
import {FlowStep} from "../Constants/flow-step";
import {useValidationContext} from "../CommonInterfaces/validation-context";

interface Props {
    resetNextClicked: Function;
    rootUserChoices: UserChoices;
    updateStatus: Function;
}

const ImportSamlFromFile = ({resetNextClicked, rootUserChoices, updateStatus}: Props) => {
    const [samlMetadataFile, setSamlMetadataFile] = useState<ReactElement>()
    const { validations, setValidations } = useValidationContext();
    useEffect(() => {
        setSamlMetadataFile(<SamlMetadataFile resetNextClicked={resetNextClicked}
                                              rootUserChoices={rootUserChoices}
                                              updateStatus={updateStatus}
                                              maxKBSize={ 100 }></SamlMetadataFile>);

        const updateValidations = {...validations}

        updateValidations.IMPORT_SAML_FROM_FILE = {
            "serviceProviderEntityId": {
                inputType: 'GENERAL',
                variant: 'default',
                message: 'The Entity ID that will be used to uniquely identify the SAML Service Provider. This might also be referred to as a "client"'
            },
            'xmlFileWithRestrictions': {
                inputType: 'GLOBAL',
                variant: 'default',
                message: ''
            }
        }

        setValidations(updateValidations);
    }, []);

    const parseVariantValue = (value: string | undefined): ("default" | "error" | undefined) => {
        if (value === 'default' || value === 'error') { return value; }
        // eslint-disable-next-line
        return undefined;
    }

    const handleSelection = (value: any) => {
        resetNextClicked(false);
        rootUserChoices.selections[value.target.id] = value.target.value;
        if (rootUserChoices.selections) { updateStatus(FlowStep.FINAL_STEP); }
    }

    return <Form>
        <Title headingLevel="h2">Import SAML from file</Title>
        <FormGroup
            label="Service provider entity ID"
            isRequired
            onChange={(value) => { handleSelection(value) }}>
            <TextInput
                type="text"
                id={SERVICE_PROVIDER_ENTITY_ID} />
            <HelperText>
                <HelperTextItem variant={ parseVariantValue(validations.IMPORT_SAML_FROM_FILE?.serviceProviderEntityId?.variant) }>
                    { validations.IMPORT_SAML_FROM_FILE?.serviceProviderEntityId?.message }
                </HelperTextItem>
            </HelperText>
        </FormGroup>
        <FormGroup label="File Upload" isRequired>
            {samlMetadataFile}
            <HelperText>
                <HelperTextItem variant={ parseVariantValue(validations.IMPORT_SAML_FROM_FILE?.xmlFileWithRestrictions?.variant) }>
                    { validations.IMPORT_SAML_FROM_FILE?.xmlFileWithRestrictions?.message }
                </HelperTextItem>
            </HelperText>
        </FormGroup>
    </Form>
}

ImportSamlFromFile.propTypes = {
    rootUserChoices: PropTypes.object,
    updateStatus: PropTypes.func
}

export default ImportSamlFromFile;
